<template>
    <div class="meitu">
         <div class="gif" v-for="(item, index) in meitu" :key="index">
            <img :src='item.gif?item.gif : thumbnail' alt="">
         </div>
    </div>
</template>
<script>
export default {
  data () {
    return {
      meitu: [],
      page: 1
    }
  },
  mounted () {
    this.getMeituData()
  },
  methods: {
    getMeituData () {
      var url = 'https://www.apiopen.top/satinGodApi?type=4&page=' + this.page
      this.$http.get(url).then(res => {
        this.meitu = res.body.data
      })
    }
  }
}
</script>
<style>
.meitu {
    position: fixed;
    /*border: 1px solid red;*/
    /*width: 100%;*/
    overflow: auto;
    top: 50px;
    bottom: 50px;
    left: 0;
    right: 0;
}
.gif {
  width: 100%;
  height: 250px;  
  margin-bottom: 20px;    
}
.gif img {
  width: 100%;
  height: 250px;
  display: block;
  margin-bottom: 20px;
}
</style>



